/* eslint-disable react/no-unescaped-entities */
/* eslint-disable react/no-array-index-key */
import React from "react"
import tw from "tailwind.macro"
import styled from "styled-components"
import SwipeableViews from "react-swipeable-views"
import { ContentFull, Container, Inner } from "../elements/Content"
import { Title, H5, P, WrapTextBlock, Highlight } from "../elements/Text"
import ValuesGraphicOne from "../images/vg1.svg"
import ValuesGraphicTwo from "../images/vg2.svg"
import ValuesGraphicThree from "../images/vg3.svg"

const ContentWrapper = styled(ContentFull)`
  ${tw`bg-white`};
`

const Slide = styled(Container)`
  ${tw`pb-8`};
`

const SlideInner = styled(Inner)`
  ${tw`lg:flex lg:flex-wrap`};
`

const SlideText = styled.div`
  ${tw`block w-full lg:w-1/3 lg:mr-auto lg:pr-12 xl:pr-24 lg:pt-8`};
  ${props => props.lg && tw`lg:w-2/3 `}
  ${props => props.md && tw`lg:w-1/2 `}

  ${P} {
    ${tw`text-xl md:text-2xl font-light text-black`};
  }
`
const SlideGraphic = styled.div`
  ${tw`w-2/3 lg:w-1/3 mx-auto lg:mx-0 text-grey-darkest`};
  ${props => props.lg && tw`w-full lg:w-1/2 `}
`

const Tabs = styled(Inner)`
  ${tw`list-reset flex sm:justify-center lg:justify-start py-2 lg:mr-auto`}
`

const TabsContainer = styled(Container)`
  ${tw`px-0`}
`
const TabsItem = styled.li`
  ${tw`px-2 lg:px-0 lg:whitespace-no-wrap`};

  &:not(:last-child)::after {
    @media only screen and (min-width: 901px) {
      ${tw`text-grey-darkest pr-6`};
      content: "/";
    }
  }
`

const TabsItemsLink = styled(H5)`
  ${tw`inline-block py-1 pl-2 pr-2 md:pl-0 md:pr-6 mb-0 cursor-pointer`};
  ${tw`text-xs text-grey hover:text-grey-darkest leading-tight`};
`

// TODO: convert to use Hooks
export default class ValuesContent extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      current: 0,
    }
  }

  isActiveStyles = isCurrent => {
    const { current } = this.state

    if (current === isCurrent) {
      return { color: "#2f392e" }
    }

    return {}
  }

  handleChangeIndex = index => {
    this.setState({
      current: index,
    })
  }

  render() {
    const { current } = this.state
    return (
      <>
        <ContentWrapper id="values">
          <TabsContainer>
            <Tabs>
              {[
                "Shared ambition",
                "Deep understanding",
                "Creative execution",
              ].map((item, index) => (
                <TabsItem key={index}>
                  <TabsItemsLink
                    key={index}
                    style={this.isActiveStyles(index)}
                    onClick={() => this.handleChangeIndex(index)}
                  >
                    {item}
                  </TabsItemsLink>
                </TabsItem>
              ))}
            </Tabs>
          </TabsContainer>
          <SwipeableViews
            enableMouseEvents
            animateHeight={false}
            index={current}
            onChangeIndex={this.handleChangeIndex}
          >
            <Slide>
              <SlideInner>
                <SlideText lg>
                  <Title>
                    <WrapTextBlock>
                      Shared
                      <br />
                      <Highlight>Ambition</Highlight>
                    </WrapTextBlock>
                  </Title>
                  <P>
                    A shared vision, a shared responsibility and a shared
                    ambition to make things better.
                  </P>
                  <P>
                    We've always shared our client's belief in better customer
                    experiences, in better performance and in a better product.
                  </P>
                </SlideText>
                <SlideGraphic>
                  <ValuesGraphicOne />
                </SlideGraphic>
              </SlideInner>
            </Slide>
            <Slide>
              <SlideInner>
                <SlideText lg>
                  <Title>
                    <WrapTextBlock>
                      Deep
                      <br />
                      <Highlight>Understanding</Highlight>
                    </WrapTextBlock>
                  </Title>
                  <P>
                    We value a rich understanding of our clients' products,
                    their goals and their businesses.
                  </P>
                  <P>
                    We believe we produce much better results by listening,
                    experimenting and asking the right questions.
                  </P>
                </SlideText>
                <SlideGraphic>
                  <ValuesGraphicTwo />
                </SlideGraphic>
              </SlideInner>
            </Slide>
            <Slide>
              <SlideInner>
                <SlideText lg>
                  <Title>
                    <WrapTextBlock>
                      Creative
                      <br />
                      <Highlight>Execution</Highlight>
                    </WrapTextBlock>
                  </Title>
                  <P>
                    Good execution is about being rigorous, being open minded
                    and applying hard won experience.
                  </P>
                  <P>
                    We've demonstrated with all of our clients that we're able
                    to think beyond the immediate problem domain and build high
                    quality and sustainable software.
                  </P>
                </SlideText>
                <SlideGraphic>
                  <ValuesGraphicThree />
                </SlideGraphic>
              </SlideInner>
            </Slide>
          </SwipeableViews>
        </ContentWrapper>
      </>
    )
  }
}
