/* eslint-disable react/no-array-index-key */
import React from "react"
import { StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import styled from "styled-components"
import tw from "tailwind.macro"

export const processImage = graphql`
  fragment processImage on File {
    childImageSharp {
      fluid(maxHeight: 60) {
        ...GatsbyImageSharpFluid
      }
    }
  }
`

const ImageList = styled.div`
  ${tw`flex flex-wrap items-center justify-center`};
  margin-left: -30px;
  margin-right: -30px;
`

const ImageWrapper = styled.div`
  ${tw`my-8 mx-6`};
  width: 75px;

  @media only screen and (min-width: 901px) {
    width: 100px;
  }
`

const Image = styled(Img)`
  filter: grayscale(90%);
  opacity: 0.75;
`

const ClientImages = () => (
  <StaticQuery
    query={graphql`
      query clientsImgQuery {
        source: allFile(
          filter: {
            absolutePath: { regex: "/data/case-studies/\/?(?:[^\/]+\/?)\\logo.*$/" }
          }
        ) {
          edges {
            node {
              name
              ...processImage
            }
          }
        }
      }
    `}
    render={data => {
      return (
        <ImageList>
          {data.source.edges.map(({ node }, i) => {
            return (
              <ImageWrapper key={`wrap${i}`}>
                <Image
                  key={i}
                  fluid={node.childImageSharp.fluid}
                  style={{ maxHeight: 60 }}
                  imgStyle={{ objectFit: "contain" }}
                />
              </ImageWrapper>
            )
          })}
        </ImageList>
      )
    }}
  />
)
export default ClientImages
