/* eslint-disable no-undef */
/* eslint-disable react/no-unescaped-entities */
/* eslint-disable react/no-array-index-key */
import React, { useState } from "react"
import { StaticQuery, graphql, navigate } from "gatsby"
import tw from "tailwind.macro"
import styled, { css } from "styled-components"
import SwipeableViews from "react-swipeable-views"
import { ContentFull, Container, Inner } from "../elements/Content"
import { H5, H1Light, PLight } from "../elements/Text"
import IconQuote from "../images/icons/quote.svg"

const ContentWrapper = styled(ContentFull)`
  ${tw`bg-green-darkest`};
`

const Slide = styled(Container)`
  ${tw`pb-12 md:pb-16 xxl:pb-32`};
`

const SlideInner = styled(Inner)`
  ${tw`lg:flex lg:flex-wrap`};
`

const SlideContent = css`
  ${tw`w-full xl:w-2/3 xl:mx-auto`};
`

const SlideTitle = styled(H1Light)`
  ${SlideContent};
`

const SlideText = styled(PLight)`
  ${SlideContent};
  ${props =>
    props.smallText ? tw`text-base md:text-xl` : tw`text-xl md:text-2xl`}
  ${tw`font-light`};
`

const SlideFooter = styled(PLight)`
  ${SlideContent};
`

const Tabs = styled(Inner)`
  ${tw`list-reset flex justify-center pt-2 lg:m-auto`}
`
const TabsItem = styled.li`
  ${tw`px-3 lg:px-0`};

  &:not(:last-child)::after {
    @media only screen and (min-width: 901px) {
      ${tw`mr-6`};
      content: "";
    }
  }
`

const TabsItemsLink = styled(H5)`
  ${tw`inline-block rounded-full bg-grey hover:bg-white cursor-pointer`};
  ${tw`w-6 h-6 lg:w-4 lg:h-4`};
`

const IconWrapper = styled.span`
  ${tw`inline-block`};
`
const IconContainer = styled.span`
  ${tw`rounded-full bg-pink text-white`};
  ${tw`w-8 h-8 md:w-10 md:h-10 mr-2`};
  ${tw`flex items-center justify-center`};
`

const Icon = styled(IconQuote)`
  width: 60%;
  height: 60%;
`
let autoScroll = true

const TestimonialList = data => {
  const items = data.data.allTestimonialsJson.edges
  const itemsCount = items.length

  const [current, setCurrent] = useState(0)
  const isActive = index => current === index
  const changeTo = index => {
    if (typeof window !== "undefined") {
      setCurrent(index)
      // const destinationElementTop = document.querySelector("#testimonials").offsetTop;
      // window.scrollTo({ behavior: "smooth", top: destinationElementTop });
    } else {
      navigate("#testimonials")
    }
  }
  React.useEffect(() => {
    let timer
    if (autoScroll) {
      timer = setTimeout(() => {
        changeTo((current + 1) % itemsCount)
      }, 7000)
    }
    return () => {
      clearTimeout(timer)
    }
  })

  return (
    <>
      <Container>
        <Tabs>
          {Array(itemsCount)
            .fill()
            .map((_, index) => (
              <TabsItem key={index}>
                <TabsItemsLink
                  key={index}
                  onClick={() => {
                    autoScroll = false
                    changeTo(index)
                  }}
                  style={isActive(index) ? { backgroundColor: "white" } : {}}
                />
              </TabsItem>
            ))}
        </Tabs>
        <SwipeableViews
          enableMouseEvents
          animateHeight={false}
          index={current}
          onChangeIndex={index => {
            autoScroll = false
            changeTo(index)
          }}
        >
          {items.map(item => (
            <Slide key={item.node.title}>
              <SlideInner>
                <SlideTitle>{item.node.title}</SlideTitle>
                <SlideText smallText={item.node.smallText}>
                  <IconWrapper>
                    <IconContainer>
                      <Icon />
                    </IconContainer>
                  </IconWrapper>
                  {item.node.content}
                </SlideText>
                <SlideFooter>{item.node.author}</SlideFooter>
              </SlideInner>
            </Slide>
          ))}
        </SwipeableViews>
      </Container>
    </>
  )
}

const TestimonialsContent = () => (
  <StaticQuery
    query={graphql`
      query testimonialsQuery {
        allTestimonialsJson {
          edges {
            node {
              title
              content
              author
              smallText
            }
          }
        }
      }
    `}
    render={data => (
      <>
        <ContentWrapper id="testimonials">
          <TestimonialList data={data} />
        </ContentWrapper>
      </>
    )}
  />
)

export default TestimonialsContent
